@import '../variables.scss';


header {
    background-color: $lavender;
    color: $berry-red;
    display: flex;
    align-items: center;
    max-height: 5rem;
    font-family: "Quattrocento Sans", sans-serif;
    font-weight: bold;
    border-bottom: 1px solid rgb(0, 0, 0, 0.3);
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;


    nav {
        display: flex;
        height: 5rem;
        gap: 60vw;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        .svg{
            height: 2rem;
            width: 2rem;
        }



        .nav-name {
            font-size: 1rem;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            
        }

        .svg-closed {
            height: 1rem;
            width: 1rem;
            cursor: pointer;
            transition: background-color 300ms;
            padding: 1rem 1rem 1rem 1rem;
            border-radius: 50%;
        }

        .svg-open {
            @extend .svg-closed;
            background-color: $primary;
        }

      
        
        .dropdown-menu {

      
            border-top: 1px solid rgb(0, 0, 0, 0.3);;
            top: 5rem;
            background-color: $lavender;
            right: 0;
            display: grid;
            grid-template-rows: repeat(5, 5rem);
            position: absolute;
            width: 70vw;
            height: 100vh;
            z-index: 100;


            .link-elem {
                display: flex;
                align-items: center;
                padding-left: 1rem;
                text-decoration: none;
                color: darken($berry-red, 5%);
                font-size: 1rem;
                border-bottom: 1px solid rgb(0, 0, 0, 0.3);
                font-weight: bold;
                
                
            }


   
        }


    }
}