@import '../variables.scss';

.home-wrapper{
    display: flex;
    flex-direction: column;
    width: auto;

    .description {
        width: auto;
        height: 30vh;
        display: flex;
        flex-direction: column;
        text-align: center;
        
        background-image: url(../../assets/background.png);
        padding-bottom: 2rem;

        color: white;
        background-size: cover; 
  


        .description-title {
            padding-top: 2.5rem;
            position: relative;
            top: 30;
            font-size: 2rem;
            margin-bottom: 1.5rem;
        }
        .description-text{
            position: relative;
            font-size: 1.1rem;
            
        }
     }


}