@import '../variables.scss';

.carousel-wrapper {
    display: flex;
    height: auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 3rem;
    padding-bottom: 2rem;
    background-color: $mint-green;

    .title {
        font-size: 1.5rem;
        font-weight: bold;
        text-align: center;
    }

    .carousel {
        margin-top: 3rem;
        column-gap: 1rem;
        position: relative;
        display: flex;
        width: 100vw;
        align-items: center;
        justify-content: center;


        img {
            width: 15rem;
            border-radius: 0.25rem;
            box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
        }
        
        .inactive {
            display: none;
        }
    }

    .name {
        margin-top: 1rem;
        font-size: 1.2rem;
    }




}